import React, { useEffect, useState, useRef } from "react";
import Footer from "../components/footer";
import "../css/chat.css";

import VerywiseLogo from "../images/verywiselogo.svg";
import TelegramLogo from "../images/telegramlogo.svg";
import Lares from "../images/twitter_lares.png";

function isValidHttpUrl(string) {
  //from StackOverflow https://stackoverflow.com/questions/5717093/check-if-a-javascript-string-is-a-url
  let url;
  try {
    url = new URL(string);
  } catch (_) {
    return false;
  }
  return url.protocol === "http:" || url.protocol === "https:";
}

const SPECIAL_CHARS = /([!@#$%^&*?.,])/g;
var link_ready_word = "";
function verifyLink(word_token, show_card) {
  if (isValidHttpUrl(word_token) == false) {show_card.current=false;return false;}
  link_ready_word =
    word_token[word_token.length - 1].match(SPECIAL_CHARS) == null
      ? word_token
      : word_token.substr(0, word_token.length - 1);
  return true;
}

function verify_if_signup(link,show_card) {
  const end_character = link != link_ready_word ? link[link.length - 1] : "";
  if (link_ready_word == "https://member.verywise.io/register"){
    show_card.current=true;console.log("HAS TARGET LINK");
    return "SignUp Link" + end_character;
  }
  show_card.current=false;
  return link;
}

const IndexPage = () => {
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [takingTooLong, setTakingTooLong] = useState(false);
  const [notTooLong, setNotTooLong] = useState(false);

  useEffect(() => {
    const params = new Proxy(new URLSearchParams(window.location.search), {
      get: (searchParams, prop) => searchParams.get(prop),
    });

    if (params.q) {
      callApiWithQueryParam(params.q);
    }
  }, []);

  useEffect(() => {
    const chatWindow = document.querySelector(".chat-window");

    chatWindow.scrollTo({
      top: chatWindow.scrollHeight,
      left: 0,
      behavior: "smooth",
    });
  }, [messages]);

  const callApiWithQueryParam = async (input) => {
    const messagesWithNewInput = [
      ...messages,
      { key: messages.length, message: input, type: "query" },
    ];
    setMessages(messagesWithNewInput);
    setInputValue("");

    const time1 = setTimeout(() => {
      setTakingTooLong(true);
    }, 6000);

    const time2 = setTimeout(() => {
      setNotTooLong(true);
    }, 10000);

    try {
      setIsLoading(true);
      const response = await callApi(input);
      const jsonData = await response.json();
      setMessages([
        ...messagesWithNewInput,
        {
          key: messagesWithNewInput.length,
          message: jsonData,
          type: "response",
        },
      ]);
      setIsLoading(false);
      setTakingTooLong(false);
      setNotTooLong(false);
      clearTimeout(time1);
      clearTimeout(time2);
    } catch (e) {
      setIsLoading(false);
      setTakingTooLong(false);
      setNotTooLong(false);
      clearTimeout(time1);
      clearTimeout(time2);
    }
  };

  const callApi = async (input) => {
    const response = await fetch(
      "https://guarded-plains-39059.herokuapp.com/web",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          query: input,
        }),
      }
    );

    return response;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const messagesWithNewInput = [
      ...messages,
      { key: messages.length, message: inputValue, type: "query" },
    ];
    setMessages(messagesWithNewInput);
    setInputValue("");

    const time1 = setTimeout(() => {
      setTakingTooLong(true);
    }, 10000);

    const time2 = setTimeout(() => {
      setNotTooLong(true);
    }, 6000);

    try {
      const response = await callApi(inputValue);
      const jsonData = await response.json();
      setMessages([
        ...messagesWithNewInput,
        {
          key: messagesWithNewInput.length,
          message: jsonData,
          type: "response",
        },
      ]);
      setIsLoading(false);
      setTakingTooLong(false);
      setNotTooLong(false);
      clearTimeout(time1);
      clearTimeout(time2);
    } catch (e) {
      setIsLoading(false);
      setTakingTooLong(false);
      setNotTooLong(false);
      clearTimeout(time1);
      clearTimeout(time2);
    }
  };

  const show_card = useRef(false);

  return (
    <>
      <main>
        <nav>
          <a href="/">Home</a>
          <a href="/about">About</a>
          <a
            href="https://t.me/renter_support_bot"
            target="_blank"
            rel="noopener"
          >
            Telegram{" "}
            <img
              className="telegram-logo"
              src={TelegramLogo}
              height={22}
              width={22}
              alt="Telegram"
            />
          </a>
        </nav>
        <div>
          <header>
            <h1>
              <a href="/">
                <img
                  className="logo"
                  src={VerywiseLogo}
                  alt="Very Wise - Be Empowered"
                />
              </a>
              <span className="invisible">Very Wise - Be Empowered</span>
            </h1>
            <h2>Be Empowered</h2>
          </header>

          <section className="chat-window">
            {messages.map((msg) => {
              if (msg.type === "response") {
                return (
                  <>
                    <div
                      className={`chat-window__message-right animate__animated animate__fast animate__fadeInRight`}
                      key={msg.key}
                    >
                      {msg.message
                        .split(/[\u0000-\u001F\u007F-\u009F ]/g)
                        .map((word_token) => (
                          <span>
                            &nbsp;
                            {verifyLink(word_token,show_card) ? (
                              <a
                                key={word_token}
                                target="_blank"
                                rel="noreferrer"
                                href={link_ready_word}
                              >
                                {verify_if_signup(word_token,show_card)}
                              </a>
                            ) : (
                              <span>{word_token}</span>
                            )}
                          </span>
                        ))}
                    </div>
                    {(show_card.current)&&
                    <div className={`signupCard  animate__animated animate__fast animate__fadeInRight`} onClick={()=>window.open('https://member.verywise.io/register')}>
                        <img
                            className="logo__card"
                            src={VerywiseLogo}
                            alt="Very Wise - Be Empowered"
                            />
                        <div className={`signupCard__textBox`}>SignUp on VeryWise by clicking this card!</div>
                    </div>}
                  </>
                );
              } else {
                return (
                  <div
                    className={`chat-window__message-left animate__animated animate__fast animate__fadeInLeft`}
                    key={msg.key}
                  >
                    {msg.message}
                  </div>
                );
              }
            })}
            {isLoading && (
              <div
                className={`chat-window__message-loading animate__animated animate__fast animate__fadeInRight`}
                key={`loading`}
              >
                <span className="loading">
                  {takingTooLong
                    ? "I'm gathering the information, won't be long now"
                    : notTooLong
                    ? "Sorry, I'm taking a little extra time than normal"
                    : "Please wait whilst I reply"}
                </span>
              </div>
            )}
          </section>
          <section className="search-bar animate__animated animate__fast animate__fadeInDown">
            <form onSubmit={handleSubmit}>
              <input
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                className="chat__input"
                type="text"
                placeholder={
                  messages && !isLoading
                    ? `Did that answer your query?`
                    : `Ask me anything about renting`
                }
              />
              <input
                className="chat__submit"
                type="submit"
                value="Search"
                disabled={isLoading || !inputValue}
              />
            </form>
          </section>
        </div>
      </main>
      <Footer />
    </>
  );
};

export default IndexPage;

export const Head = () => (
  <>
    <title>Very Wise - Be Empowered</title>
    <meta
      name="description"
      content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`}
    />
    <meta name="image" content={`https://www.verywise.io/${Lares}`} />
    <meta name="twitter:card" content="summary_large_image" />
    <meta name="twitter:title" content="Very Wise - Be Empowered" />
    <meta name="twitter:url" content="https://www.verywise.io" />
    <meta
      name="twitter:description"
      content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`}
    />
    <meta name="twitter:image" content={`https://www.verywise.io/${Lares}`} />
    <meta name="twitter:creator" content="veriwise" />
  </>
);
